import React, { Component } from 'react';
import $ from 'jquery';
import './Ripples.js';

export default class Home extends Component {
	constructor(props) {
		super(props);
		this.state = {
			height: window.innerHeight
		}
	}

	componentDidMount() {
		$('#home').ripples({
      resolution: 512,
      dropRadius: 10, 
      perturbance: 0.02
    });
    window.addEventListener('resize', (e) => {
      this.setState({height: window.innerHeight});
    });
	}

	render = () => {
		const fname = "Alex";
		const lname = "Figliolia";
		const t = "Front End";
		const d = "Developer";
		return (
			<section 
				className="home" 
				id="home">
				<h2>
					{
						fname.split('').map((letter, i) => {
							return <div key={i}>{letter}</div>
						})
					}
				</h2>
				<h2>
					{
						lname.split('').map((letter, i) => {
							return <div key={i}>{letter}</div>
						})
					}
				</h2>
				<div>
					<div>
						<h2>
							{
								t.split('').map((letter, i) => {
									if(letter === ' ') {
										return <div key={i}>&nbsp;&nbsp;</div>
									} else {
										return <div key={i}>{letter}</div>
									}
								})
							}
						</h2>
						<h2>
							{
								d.split('').map((letter, i) => {
									return <div key={i}>{letter}</div>
								})
							}
						</h2>
					</div>
					<button>
						<h3>Work</h3>
					</button>
				</div>
			</section>
		);
	}
}



